export class ParticleStaticEngine extends AbstractParticleEngine {
    particleControllers: Array<ParticleController>
    constructor(particleCount: number = 100, geometry: THREE.BufferGeometry, material: THREE.Material, 
        setInitPosition: (index: number) => THREE.Vector3,
        setInitRotation: (index: number) => THREE.Euler,
        setInitScale: (index: number) => number,
        setInitColor: (index: number) => THREE.Color,
    ) {
        super(particleCount, geometry, material)
        this.particleControllers = []
        for (let i = 0; i < particleCount; i++) {
            let initPosition = setInitPosition(i)
            let initRotation = setInitRotation(i)
            let initScale = setInitScale(i)
            let initColor = setInitColor(i)
            let tempParticleController = new ParticleController(initPosition, initRotation, initScale, initColor, i)
            this.particleControllers.push(
                tempParticleController
            )
            tempParticleController.resetGeom(this.mesh)
        }
    }
}


export class ParticleRuleEngine extends AbstractParticleEngine {
    isInTransition: boolean
    particleControllers: Array<ParticleRuleController>
    animRuleFunc: (rule: ParticleAnimRuleIn) => ParticleAnimRuleOut
    constructor(particleCount: number = 100, geometry: THREE.BufferGeometry, material: THREE.Material, 
        setInitPosition: (index: number) => THREE.Vector3,
        setInitRotation: (index: number) => THREE.Euler,
        setInitScale: (index: number) => number,
        setInitColor: (index: number) => THREE.Color,
        setInitMixColor: (index: number) => THREE.Color,
        animRuleFunc: (rule: ParticleAnimRuleIn) => ParticleAnimRuleOut
    ) {
        super(particleCount, geometry, material)
        this.isInTransition = false
        this.particleControllers = []
        this.animRuleFunc = animRuleFunc
        for (let i = 0; i < particleCount; i++) {
            let initPosition = setInitPosition(i)
            let initRotation = setInitRotation(i)
            let initScale = setInitScale(i)
            let initColor = setInitColor(i)
            let initMixColor = setInitMixColor(i)
            let tempParticleController = new ParticleRuleController(
                initPosition, initRotation, initScale, initColor, initMixColor, i
            )
            this.particleControllers.push(
                tempParticleController
            )
            tempParticleController.resetGeom(this.mesh)
        }
    }
    transit(transitionTime: number = 1000) {
        if (this.isInTransition) {
            return
        }
        const self = this
        controller.tweenNumber(0, 1, (interp) => {
            self.particleControllers.forEach(pc => {
                pc.updateGeomByRule(self.mesh, interp, self.animRuleFunc)
            })
            self.mesh.instanceMatrix.needsUpdate = true
            if (self.mesh.instanceColor) {
                self.mesh.instanceColor.needsUpdate = true
            }
        },
        () => {
            self.isInTransition = false
            // self.particleControllers.forEach(pc => {
            //     pc.resetGeom(self.mesh, self.dummy)
            // })
            self.mesh.instanceMatrix.needsUpdate = true
            if (self.mesh.instanceColor) {
                self.mesh.instanceColor.needsUpdate = true
            }
        }, transitionTime)
    }
}