import * as THREE from 'three'
import * as TWEEN from '@tweenjs/tween.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass'
import { Stats } from 'three-stats'
import { TDSLoader } from 'three/examples/jsm/loaders/TDSLoader'
import { CustomCSS3DObject, CustomCSS3DSprite, CustomCSS3DRenderer } from '../Three/CustomCSS3DRenderer'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'
import { DampingObjectController, DampingVector3Controller } from '../Three/Utils'
// import { RefractionMesh, RefractionManager } from '../Three/Refraction2.0'
import { RefractionObject, RefractionManager } from '../ThreeRealisticRefraction/ThreeRealisticRefraction'
import { RoundedBoxGeometry } from 'three/examples/jsm/geometries/RoundedBoxGeometry'
import { controller } from '../AnimUtils/TweenUtils'
import * as PE from './ParticleEngine'
import { randn_bm } from '../Three/Utils'
import { SQWater } from '../Three/SQWater'

interface ParticleEnginePair {
    mesh: THREE.Mesh,
    pe: PE.AbstractParticleEngine
}

class FrontPageManager {
    container: HTMLElement
    renderer: THREE.WebGLRenderer
    // controls: OrbitControls
    cardInsideScene: THREE.Scene
    scene: THREE.Scene
    composer: EffectComposer
    stats: Stats
    // controls: OrbitControls


    refractionManager: RefractionManager

    // cursor position
    mousePosX: number
    mousePosY: number
    pointer: THREE.Vector2

    // camera
    camera: THREE.PerspectiveCamera
    cameraZPosition: number
    cameraLootAtController: DampingVector3Controller
    cameraTargetPosition: THREE.Vector3
    cameraWithMouseMoveFactor: number


    // intersections
    raycaster: THREE.Raycaster
    intersectGlassObjs: Array<THREE.Object3D>

    // glass
    glassPosRadius: number
    glassSelected: THREE.Mesh | undefined
    glassIsRotating: Boolean

    // css
    cssScene: THREE.Scene
    cssRenderer: CustomCSS3DRenderer | undefined

    // loading manager
    loadingManager: THREE.LoadingManager

    leavesParticleCount: number
    leavesMatrixDummy: THREE.Object3D
    leavesParticlesMeshes: Array<THREE.InstancedMesh>

    particleEngineDict: Array<ParticleEnginePair>

    constructor(_container: HTMLElement,
        onLoad?: () => void,
        onProgress?: (url: string, loaded: number, total: number) => void,
        onError?: (url: string) => void
        ) {
        const self = this

        // *********** basic settings ***********
        this.cameraZPosition = 500
        this.container = _container
        this.camera = new THREE.PerspectiveCamera( 45, window.innerWidth / window.innerHeight, 1, 5000 )
        this.renderer = new THREE.WebGLRenderer( {alpha: true} )
        this.renderer.setClearColor( 0xffffff )
        this.renderer.setPixelRatio( window.devicePixelRatio )
        this.renderer.setSize(window.innerWidth, window.innerHeight)
        this.renderer.domElement.style.position = 'absolute'
        this.renderer.domElement.style.top = '0px'
        this.renderer.domElement.style.left = '0px'
        this.container.appendChild( this.renderer.domElement )
        // TODO: (debug stats)
        this.stats = new Stats()
        this.stats.domElement.style.zIndex = '100'
        this.container.appendChild( this.stats.domElement )
        // this.controls = new OrbitControls(this.camera, this.container)
        this.camera.position.set(0, 200, 200)

        this.scene = new THREE.Scene()
        this.cssScene = new THREE.Scene()
        this.cardInsideScene = new THREE.Scene()

        // 
        this.loadingManager = new THREE.LoadingManager(() => {
            if (onLoad) {
                onLoad()
            }
        }, (url: string, loaded: number, total: number) => {
            if (onProgress) {
                onProgress(url, loaded, total)
            }
        }, (url) => {
            if (onError) {
                onError(url)
            }
        })

        // *********** background & env ***********
        const r = './cube1/'
        const urls = [
            r + 'px.jpg', r + 'nx.jpg',
            r + 'py.jpg', r + 'ny.jpg',
            r + 'pz.jpg', r + 'nz.jpg'
        ]
        const textureCube = new THREE.CubeTextureLoader(this.loadingManager).load( urls )
        textureCube.mapping = THREE.CubeRefractionMapping
        // this.scene.background = textureCube
        this.scene.environment = textureCube



        // *********** composers & refraction managers ***********
        this.refractionManager = new RefractionManager(this.scene, this.container, this.renderer, this.camera)
        const renderPass = new RenderPass(this.scene, this.camera)
        renderPass.clear = true
        renderPass.clearDepth = false
        this.composer = new EffectComposer(this.renderer, this.refractionManager.backRenderTarget)
        this.composer.renderToScreen = false
        this.composer.addPass(renderPass)
        this.refractionManager.backRenderTarget.texture = this.composer.readBuffer.texture
        this.refractionManager.frontScene.background = this.composer.readBuffer.texture


        // *********** set light ***********
        var light = new THREE.PointLight()
        light.position.x = 0
        light.position.y = 100
        light.position.z = 0
        this.scene.add(light)

        // *********** refraction objects ***********
        
        this.particleEngineDict = []

        this.intersectGlassObjs = []
        var glassMesh2 = new THREE.Mesh(
            new RoundedBoxGeometry(40, 40, 40, 5, 5),
            // new THREE.BoxGeometry(40, 40, 40),
            new THREE.MeshPhongMaterial({
                color: new THREE.Color(0xff00ff)
            })
        )
        let glassAngle = 0.6 - Math.PI / 2
        this.glassPosRadius = 120
        glassMesh2.position.set(Math.cos(glassAngle) * this.glassPosRadius, 30, Math.sin(glassAngle) * this.glassPosRadius)
        let tx = new THREE.TextureLoader(this.loadingManager).load( './jutabag3ds/texture/normal.jpg' )
        this.refractionManager.addRefractionMesh(glassMesh2, true, tx, 0xffffff, 0xffffff, 2, 0)
        this.intersectGlassObjs.push(glassMesh2)
        const loader = new TDSLoader(this.loadingManager)
        loader.load( './dodeca/dodeca.3ds', (obj) => {
            if (obj instanceof THREE.Group && obj.children[0] instanceof THREE.Mesh) {
                var dodeca = obj.children[0]
                dodeca.scale.multiplyScalar(0.3)
                glassAngle = -0.6 - Math.PI / 2
                dodeca.position.set(Math.cos(glassAngle) * this.glassPosRadius, 30, Math.sin(glassAngle) * this.glassPosRadius)
                // dodeca.position.x += 50
                // dodeca.position.z += 50
                this.refractionManager.addRefractionMesh(dodeca, true, tx, 0xffa000, 0x00c0ff, 2, 0.8)

                this.intersectGlassObjs.push(dodeca)
                // var testInsideCube = new THREE.Mesh(
                //     new THREE.SphereBufferGeometry(5),
                //     new THREE.MeshPhongMaterial({
                //         color: new THREE.Color(0xff00ff)
                //     })
                // )
                // testInsideCube.position.copy(dodeca.position)
                // this.scene.add(testInsideCube)

                
                var pe1 = new PE.ParticleRuleEngine(300, particleGeometry, particleMaterial,
                    (_) => {
                        // return new THREE.Vector3(0)
                        let angle = 2 * Math.PI * Math.random()
                        let radiusNew = 2 + randn_bm()
                        let randomOffset = new THREE.Vector3(randn_bm() * 1, randn_bm() * 1, randn_bm() * 1)
                        return new THREE.Vector3(Math.cos(angle), 0, Math.sin(angle)).multiplyScalar(radiusNew).add(randomOffset)
                    },
                    (_) => {
                        return new THREE.Euler(0,0,0)
                    },
                    (_) => {
                        return 0
                    },
                    (_) => {
                        if (Math.random() < 0.5) {
                            return new THREE.Color(0x00c0ff)
                        }
                        return new THREE.Color(0x22ff70)
                    },
                    (_) => {
                        return new THREE.Color(0xffff00)
                    },
                    (ruleIn: PE.ParticleAnimRuleIn) => {
                        let rotateAngle = Math.atan(ruleIn.originalPosition.z / ruleIn.originalPosition.x)
                        if (ruleIn.originalPosition.x < 0) rotateAngle += Math.PI
                        rotateAngle -= ruleIn.interp * ruleIn.interp
                        let posExpand = new THREE.Vector3(
                            Math.cos(rotateAngle) * 10 + ruleIn.originalPosition.x,
                            0,
                            Math.sin(rotateAngle) * 10 + ruleIn.originalPosition.z).multiplyScalar(ruleIn.interp * 3 + 1 + ruleIn.randomFactor)

                        let ruleOut = {
                            curPosition: posExpand,
                            curRotation: new THREE.Euler(1.0, 0.0, 1.0),
                            curScale: ruleIn.interp * (1 - ruleIn.interp) * 4,
                            curColor: (new THREE.Color()).copy(ruleIn.originalColor).lerp(ruleIn.mixColor, ruleIn.interp * ruleIn.interp)
                        }
                        return ruleOut
                    }
                )
                pe1.mesh.position.copy(dodeca.position)
                pe1.mesh.position.y -= 15
                this.scene.add(pe1.mesh)
                this.particleEngineDict.push({
                    mesh: dodeca,
                    pe: pe1
                })
            }
        })


        // *********** some leaves ***********
        this.leavesParticleCount = 50
        this.leavesMatrixDummy = new THREE.Object3D()
        this.leavesParticlesMeshes = []

        const material = new THREE.MeshNormalMaterial({
            side: THREE.DoubleSide
        })
        loader.load( './particles/leaves.3ds', (obj) => {
            if (obj instanceof THREE.Group) {
                console.log(obj)
                obj.children.forEach(leaf => {
                    // if (leaf instanceof THREE.Mesh) {
                    //     const material = new THREE.MeshNormalMaterial({
                    //         side: THREE.DoubleSide
                    //     });
                    //     leaf.material = material
                    //     this.scene.add(leaf)
                    // }
                    if (leaf instanceof THREE.Mesh) {
                        // var mesh = new THREE.InstancedMesh( leaf.geometry, material, this.leavesParticleCount )
                        var pe = new PE.ParticleStaticEngine(self.leavesParticleCount, leaf.geometry, material,
                            self.getRandomLeavePosition,
                            self.getRandomLeaveRotation,
                            self.getRandomLeaveScale,
                            (_) => new THREE.Color(0xff00000)
                        )
                        // for (let i = 0; i < this.leavesParticleCount; i++) {
                        //     this.leavesMatrixDummy.position.copy(this.getRandomLeavePosition())
                        //     this.leavesMatrixDummy.rotation.copy(this.getRandomLeaveRotation())
                        //     this.leavesMatrixDummy.scale.copy(this.getRandomLeaveScale())
                        //     this.leavesMatrixDummy.updateMatrix()
                        //     mesh.setMatrixAt(i, this.leavesMatrixDummy.matrix)
                        // }
                        // // mesh.instanceMatrix.setUsage( THREE.DynamicDrawUsage ); // will be updated every frame
                        
                        self.scene.add( pe.mesh )
                        // this.leavesParticlesMeshes.push(mesh)
                    }
                })
                // obj.clear()
                
            }
            // contentGroupInTardisCard1.add(obj)
        })

        var particleGeometry = new THREE.InstancedBufferGeometry().copy(new THREE.SphereGeometry(0.5, 4))
        var particleMaterial = new THREE.MeshBasicMaterial({
            // emissive: new THREE.Color(0xffff80),
            // emissiveIntensity: 0.5
        })
        var pe2 = new PE.ParticleRuleEngine(300, particleGeometry, particleMaterial,
            (_) => {
                // return new THREE.Vector3(0)
                let angle = 2 * Math.PI * Math.random()
                let radiusNew = 60 + randn_bm()
                let randomOffset = new THREE.Vector3(randn_bm() * 10, randn_bm() * 1, randn_bm() * 10)
                return new THREE.Vector3(Math.cos(angle), 0, Math.sin(angle)).multiplyScalar(radiusNew).add(randomOffset)
            },
            (_) => {
                return new THREE.Euler(0,0,0)
            },
            (_) => {
                return 0
            },
            (_) => {
                return new THREE.Color(0xffa000)
            },
            (_) => {
                return new THREE.Color(0x50fff0)
            },
            (ruleIn: PE.ParticleAnimRuleIn) => {
                let rotateAngle = Math.atan(ruleIn.originalPosition.z / ruleIn.originalPosition.x)
                if (ruleIn.originalPosition.x < 0) rotateAngle += Math.PI
                rotateAngle += ruleIn.interp * ruleIn.interp
                let posExpand = new THREE.Vector3().copy(ruleIn.originalPosition).multiplyScalar(ruleIn.interp)

                // let posXExpand = posExpand
                let posYExpand = Math.sqrt(ruleIn.interp) * 5 - 10
                // posExpand.x += Math.cos(rotateAngle) * 20
                // posExpand.z += Math.sin(rotateAngle) * 20
                // posExpand.y = 0
                posExpand.y += posYExpand
                let ruleOut = {
                    curPosition: posExpand,
                    curRotation: new THREE.Euler(1.0, 0.0, 1.0),
                    curScale: ruleIn.interp * (1 - ruleIn.interp) * 4,
                    curColor: (new THREE.Color()).copy(ruleIn.originalColor).lerp(ruleIn.mixColor, ruleIn.interp * ruleIn.interp)
                }
                return ruleOut
            }
        )
        pe2.mesh.position.copy(glassMesh2.position)
        pe2.mesh.position.y -= 15
        this.scene.add(pe2.mesh)
        this.particleEngineDict.push({
            mesh: glassMesh2,
            pe: pe2
        })

        
        // var cubeGeo = ParticleEngine.getGeom(20)


        // var mat = ParticleEngine.genParticleMaterial(spriteAlphaMapTexture)

        // var mesh11 = new THREE.InstancedMesh(cubeGeo, mat, 8);

        // this.scene.add(mesh11);



        // *********** water ***********
        const waterGeometry = new THREE.PlaneGeometry( 2000, 2000 )
        console.log(this.composer.readBuffer)
        // const water = new THREE.Mesh( waterGeometry, new THREE.MeshPhongMaterial({
        //     color: new THREE.Color(0xfff0ff),
        //     // map: this.composer.readBuffer.texture,
        //     emissive: new THREE.Color(0x005080),
        //     emissiveIntensity: 0.6
        // }) );

        const water = new SQWater( waterGeometry, this.scene, {
            // color: params.color,
            // scale: params.scale,
            flowDirection: new THREE.Vector2( 1, 1 ),
            textureWidth: 512,
            textureHeight: 512
        } );

        water.position.y = -10;
        water.rotation.x = Math.PI * - 0.5;
        this.scene.add( water );

        // this.refractionManager.frontScene.background = textureCube


        // *********** camera movement ***********
        // this.cameraTargetPosition = new THREE.Vector3(0, 0, this.cameraZPosition)
        // this.camera.position.copy(this.cameraTargetPosition)
        // this.camera.lookAt(0, 0, 0)
        this.cameraWithMouseMoveFactor = 0.1
        this.cameraTargetPosition = new THREE.Vector3().copy(glassMesh2.position)
        this.cameraLootAtController = new DampingVector3Controller(new THREE.Vector3().copy(this.cameraTargetPosition), 0.9, 0.002)
        window.addEventListener('mousemove', e => {
            self.onMouseMove(e)
        })

        // *********** raycast intersections ***********
        this.raycaster = new THREE.Raycaster()
        this.glassSelected = undefined
        this.glassIsRotating = false


        // *********** events ***********
        this.mousePosX = 0
        this.mousePosY = 0
        this.pointer = new THREE.Vector2()
        window.addEventListener( 'resize', () => {
            self.onWindowResize()
        } )
        window.addEventListener( 'click', (e) => {
            self.onClick(e)
        } )

        // *********** final animate ***********
        this.animate()
    }

    animate = () => {
        requestAnimationFrame( this.animate )
        // damping controllers
        // this.updatecornerObject()
        this.updateCamera()
        this.render()
        this.stats.update()
        // this.controls.update()
    }

    render()
    {
        this.renderer.setRenderTarget(this.refractionManager.backRenderTarget)
        this.composer.render()

        this.refractionManager.render()
        if (this.cssRenderer) {
            this.cssRenderer.render(this.cssScene, this.camera)
        }
    }

    onWindowResize() {
        this.camera.aspect = window.innerWidth / window.innerHeight
        this.camera.updateProjectionMatrix()
        this.refractionManager.onWindowResize()
        this.renderer.setSize( window.innerWidth, window.innerHeight )
        this.composer.setSize( window.innerWidth, window.innerHeight )
        if (this.cssRenderer) {
            this.cssRenderer.setSize( window.innerWidth, window.innerHeight )
        }
    }
    
    onMouseMove(e: MouseEvent) {
        this.mousePosX = e.clientX
        this.mousePosY = e.clientY
        this.pointer.x = ( e.clientX / window.innerWidth ) * 2 - 1;
		this.pointer.y = - ( e.clientY / window.innerHeight ) * 2 + 1;

        this.raycaster.setFromCamera( this.pointer, this.camera )
        const intersects = this.raycaster.intersectObjects( this.intersectGlassObjs, false )
        if (intersects.length == 1 && !this.glassIsRotating && this.glassSelected !== intersects[0].object) {
            this.glassIsRotating = true
            const intersectObj = intersects[0].object
            const refObj = intersectObj ? intersectObj['refractionObject'] : null
            if (intersectObj instanceof THREE.Mesh && refObj instanceof RefractionObject) {
                console.log(intersects.length)
                this.cameraTargetPosition.copy(intersectObj.position)
                for (let i = 0; i < this.particleEngineDict.length; i++) {
                    const pe = this.particleEngineDict[i].pe
                    if (this.particleEngineDict[i].mesh == intersectObj && pe instanceof PE.ParticleRuleEngine) {
                        pe.transit(2000)
                        break
                    }
                }
                this.glassSelected = intersectObj
                controller.tweenEuler(this.glassSelected.rotation, new THREE.Euler(0.5,0.8,1), (v) => {
                        intersectObj.rotation.copy(v)
                    },
                    () => {this.glassIsRotating = false},
                    1000,
                    TWEEN.Easing.Exponential.InOut
                )
                controller.tweenNumber(1, 0, (val) => {
                    refObj.refractionMaterialUniforms['absorbColor'].value.b = val
                },
                () => {},
                500)
            }
            // this.glassSelected.rotation.set(1,2,1)
        }
        else if (intersects.length == 0 && this.glassSelected !== undefined) {
            this.glassIsRotating = true
            let rotateObj = this.glassSelected
            const refObj = this.glassSelected['refractionObject']
            controller.tweenEuler(this.glassSelected.rotation, new THREE.Euler(0,0,0), (v) => {
                rotateObj.rotation.copy(v)
                },
                () => {this.glassIsRotating = false},
                1000,
                TWEEN.Easing.Exponential.InOut
            )
            controller.tweenNumber(0, 1, (val) => {
                refObj.refractionMaterialUniforms['absorbColor'].value.b = val
            },
            () => {},
            500)
            this.glassSelected = undefined
        }
        // else {
        //     console.log(intersects.length, this.glassIsRotating, this.glassSelected !== undefined)
        // }
    }

    onClick(e: MouseEvent) {
        // if (this.glassSelected) {
        //     this.pe2.transit(2000)
        // }
    }

    getRaycastPos(posX: number, posY: number) {
        let x = (2.0 * posX) / window.innerWidth - 1.0
        let y = 1.0 - (2.0 * posY) / window.innerHeight
        // let z = 1.0
        // let ray_nds = new THREE.Vector3(x, y, z)
        let ray = new THREE.Vector3(x, y, 0.995)
        ray.unproject(this.camera)
        return ray
    }

    // updatecornerObject() {
    //     let targetPos = this.getRaycastPos(window.innerWidth - 200, window.innerHeight / 2)
    //     this.glassMesh1Controller.update(targetPos)
    // }

    updateCamera() {
        const relX = window.innerWidth / 2 - this.mousePosX
        const relY = window.innerHeight / 2 - this.mousePosY
        // console.log(this.cameraTargetPosition)
        const dampPos = (new THREE.Vector3(
            -relX * this.cameraWithMouseMoveFactor,
            relY * this.cameraWithMouseMoveFactor,
            0
        )).add(this.cameraTargetPosition)

        this.cameraLootAtController.update(dampPos)
        // this.cameraLootAtController.update(this.cameraTargetPosition)
        this.camera.lookAt(this.cameraLootAtController.currentVec3)
        var vel = this.cameraLootAtController.velocity
        this.camera.position.x = vel.y * this.cameraWithMouseMoveFactor
        this.camera.position.y = 200 - vel.x * this.cameraWithMouseMoveFactor
    }

    registerCSSCoordinator(htmlContainer: HTMLElement, htmlElem: HTMLElement, position: THREE.Vector3 = new THREE.Vector3(), scale: number = 1.0): CustomCSS3DObject {
        if (this.cssRenderer == undefined) {
            this.cssRenderer = new CustomCSS3DRenderer(htmlContainer)
            this.cssRenderer.setSize( window.innerWidth, window.innerHeight )
        }
        // controls = new OrbitControls(camera, cssRenderer.domElement)
        const object = new CustomCSS3DObject( htmlElem )
        object.position.copy(position)
        object.scale.multiplyScalar(scale)
        this.cssScene.add(object)
        return object
        // cssCoordinators.push(new ThreeCSSCoordinator(htmlContainer, htmlCamera, htmlElem, camera, position))
    }


    getRandomLeavePosition(index: number, radius: number = 300): THREE.Vector3 {
        let angle = randn_bm() * 0.8 - Math.PI / 2
        let radiusNew = radius + randn_bm()
        let randomOffset = new THREE.Vector3(randn_bm() * 60, randn_bm() * 5, randn_bm() * 60)
        return new THREE.Vector3(Math.cos(angle), 0, Math.sin(angle)).multiplyScalar(radiusNew).add(randomOffset)
    }

    getRandomLeaveRotation(index: number, scale: number = 10): THREE.Euler {
        return new THREE.Euler(Math.random() * scale, Math.random() * scale, 0)
    }

    getRandomLeaveScale(): number {
        return Math.random() * 0.2 + 0.2
    }

    
}


export {
    FrontPageManager
}